import { useState, memo } from 'react'
import { Box, Tabs, Tab } from '@mui/material'
import { TabContext, TabPanel } from '@mui/lab'

import TableInfo from './TableInfo'

import { googleInfo, yandexInfo, targetMyInfo } from './infoData'

const UsageInfo = () => {

  const [value, setValue] = useState("1")

  return (
    <div className="usage__wrapper">
        <h2> ,    </h2>
        <Box sx={{ width: '100%', typography: 'body1' }}>
          <TabContext value={value}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs value={value} variant="scrollable" scrollButtons="auto" aria-label="scrollable auto tabs example" onChange={(e, newValue) => setValue(newValue)}>
                <Tab label="  ?" value="1" />
                <Tab label="Google Adwords" value="2" />
                <Tab label="." value="3" />
                <Tab label="Target My.com" value="4" />
              </Tabs>
            </Box>
            <TabPanel value="1">
              <p style={{marginTop: "10px"}}>        ,  , ,           .</p>
              <p style={{marginTop: "10px"}}>  ,      . ,    utm_term={'{keyword}'},      {'{keyword}'}   ,    .</p>
              <p style={{marginTop: "10px"}}>       utm_content,         .</p>
            </TabPanel>
            <TabPanel sx={{padding: "10px 0"}} value="2">
              <h2>   : Google Adwords.</h2>
              <small><a style={{color: "#2c72b7"}} href="https://developers.google.com/adwords/api/docs/guides/valuetrack-mapping?hl=ru" target="_blank" rel="noreferrer"></a></small>
                <TableInfo arr={googleInfo} />
            </TabPanel>
            <TabPanel sx={{padding: "10px 0"}} value="3">
              <h2>   : ..</h2>
              <small><a style={{color: "#2c72b7"}} href="https://yandex.ru/support/direct/statistics/url-tags.html" target="_blank" rel="noreferrer"></a></small>
                <TableInfo arr={yandexInfo} />
            </TabPanel>
            <TabPanel sx={{padding: "10px 0"}} value="4">
              <h2>   : Target My.com.</h2>
              <small><a style={{color: "#2c72b7"}} href="https://target.my.com/help/advertisers/gettingstarted/ru#utm" target="_blank" rel="noreferrer"></a></small>
                <TableInfo arr={targetMyInfo} />
            </TabPanel>
          </TabContext>
        </Box>
    </div>
  )
}

export default memo(UsageInfo)